.RollDice{
    display: flex;
    flex-flow: column nowrap;
    min-height: 100vh;
    justify-content: center;
}
.RollDice-container{
    display: flex;
    justify-content: center;
}

.RollDice Button{
    width:15rem;
    padding:1.5rem;
    border:0px;
    border-radius: 10px;
    color:white;
    background: black;
    margin: 5rem auto;
    
}
.RollDice Button:hover {
    color: darkslateblue;
    cursor: pointer;
}